import swiperContent from './swiper-content.vue'
export default{
	components:{
		swiperContent
	},
	data() {
		return {
			itemData: {},
			itemData2:{}
		}
	}, 
	props:{
		currentIndex:Number
	},
	
	onReady() {
		this.get_type_data()
	},
	methods:{ 
		// 轮播图切换
		swiperChange(e){
			this.$emit('swiperChange',e.detail.current)
		},
		get_type_data(){
			uni.showLoading({
				title:'加载中'
			})
			wx.cloud.callFunction({
			  name: 'get_data_type',
			  
			}).then(res => {
				let data = res.result.data; 
				let data2 = res.result.data;
				
				this.itemData = data;
				this.itemData2 = data2;
				uni.hideLoading()
			})
		}
	}
	
}
